// TODO update types
/* eslint-disable @typescript-eslint/no-explicit-any */

import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Input,
  NgZone,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  SimpleChange,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import {ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR} from '@angular/forms';
import {TextfieldNestedSettings} from '../textfield/textfield.interface';
import {transition, trigger, useAnimation} from '@angular/animations';
import {Directionality} from '@angular/cdk/bidi';
import {fromEvent, ReplaySubject} from 'rxjs';
import {debounceTime, distinctUntilChanged, take, takeUntil} from 'rxjs/operators';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRxControlLabelTooltip,
  ANIMATION_NAME,
  debounce,
  getElementStyles,
  ResizeService,
  toBoolean,
  toNumber
} from '@bmc-ux/adapt-angular';
import {AdaptRadarGenericDirective, AdaptRadarService} from '@bmc-ux/adapt-radar';

let nextId = 0;
const noop = (): void => {
  return;
};

const cssTextFieldClasses = {
  FOCUS: 'is-focus',
  // REQUIRED: 'is-required',       // not used yet
  // DIRTY: 'is-dirty',
  // PRISTINE: 'is-pristine',
  // FORM_SUCCESS: 'has-success',
  // FORM_DANGER: 'has-danger',
  // VALID: 'form-control-success',
  // IN_VALID: 'form-control-danger'
};

export const ADAPT_TEXTFIELD_FORMBUILDER_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => AdaptTextFieldFormBuilderComponent),
  multi: true
};


@Component({
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.form-group]': 'true',
    '[class.textfield]': 'true',
    '[class.is-counting]': 'labelCount',
    '[class.has-danger]': 'control.touched && control.invalid',
    '[class.has-success]': '!control.invalid && validState',
    '[class.is-icons]': '(iconValid && !control.invalid && validState) || (iconInvalid && control.touched && control.invalid)'
  },
  selector: 'adapt-textfield-formbuilder',
  providers: [ADAPT_TEXTFIELD_FORMBUILDER_ACCESSOR],
  templateUrl: './textfield-formbuilder.component.html',
  styles: [`
    .textfield .label-count,
    .textfield .label-count-bottom {
      color: #999;
      font-weight: 500;
    }

    .textfield.has-danger .label-count,
    .textfield.has-danger .label-count-bottom {
      color: #f83200;
    }

    .form-control-flex {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      padding-top: .25rem;
      transition: all .3s;
    }

    .form-control-flex .form-control-feedback {
      padding-top: 0 !important;
      padding-bottom: 0 !important;
    }

    .form-control-flex__feedback {
      flex: 0 1 auto;
    }

    .label-count-bottom {
      text-align: right;
      flex: 0 0 auto;
    }

  `],
  animations: [
    trigger('validationFeedbackAnimation', [
      transition(':enter', [
        useAnimation(ANIMATION_NAME.fadeIn)
      ]),
      transition(':leave', [
        useAnimation(ANIMATION_NAME.fadeOut)
      ])
    ]),
    trigger('validationFeedbackChangeAnimation', [
      transition('* => *', [
        useAnimation(ANIMATION_NAME.opacity)
      ])
    ])
  ],
  encapsulation: ViewEncapsulation.None
})

export class AdaptTextFieldFormBuilderComponent extends AdaptRadarGenericDirective implements ControlValueAccessor, OnInit, OnChanges, AfterViewInit, OnDestroy {

  nestedSettingsDefault: TextfieldNestedSettings = {
    addOnLeft: true,
    addOnRight: false,
    addOnTextLeft: '@'
  };

  inputStyles: CSSStyleDeclaration = null;
  isTextOverflow: boolean = false;

  isRtl: boolean = false;

  /**
   * Reference to input element
   */
  @ViewChild('input', {static: false}) inputElement: ElementRef;
  @ViewChild('inputTextWidthEl', {static: false}) inputTextWidthEl: ElementRef<HTMLDivElement>;

  @Input() overflowText: boolean = true;

  /**
   * Label icon with a tooltip or popover
   */
  tooltipConfig: AdaptRxControlLabelTooltip = null;
  /**
   * The input element type {bs4 view}.
   */
  @Input() nested: boolean = false;
  /**
   *  Whether the element has settings for type 'nested'
   */
  @Input() nestedSettings: TextfieldNestedSettings;
  /**
   * Style class of the component.
   */
  @Input() customClass: string;
  /**
   * The plain text label for the input element
   */
  @Input() label: string;
  /**
   * The input element type; by default is 'text'
   */
  @Input() type: string = 'text';
  /**
   * Unique id of the element.
   */
  @Input() id: string = `adapt-textfield-${nextId++}`;
  /**
   * The icon must be a dpl-iconfont or adapt-iconfont.
   */
  @Input() icon: string;
  /**
   * Whether the input is with success icon
   */
  @Input() iconValid: boolean = false;
  /**
   * Whether the input is with error icon
   */
  @Input() iconInvalid: boolean = true;
  /**
   * TabIndex attribute of the element
   */
  @Input() tabindex: number = null;
  /**
   * Maxlength attribute of the element
   */
  @Input() maxlength: number = null;
  /**
   * Minlength attribute of the element
   */
  @Input() minlength: number = 0;
  /**
   * Placeholder attribute of the element.
   */
  @Input() placeholder: string;
  /**
   * Autocomplete attribute of the element.
   */
  @Input() autocomplete: string;
  /**
   * Additional validation patterns of the element [deprecated]
   */
  @Input() pattern: any;
  @Input() min: any;
  @Input() max: any;
  @Input() step: any;
  @Input() name: any;
  /** FormControl of input element */
  @Input() control: FormControl;
  /** Whether the element is disabled */
  @Input() disableControl: any;
  /**
   * An object used to control when error/success messages are shown
   */
  @Input() displayMessage: any;
  /**
   * Whether the input is valid
   */
  @Input() validState: boolean = false;
  /**
   * Whether the input is required. The plain text 'requiredLabel' for the input element
   */
  @Input() requiredLabel: string = '(required)';
  /**
   * Tooltip icon and additional text for it.
   */
  @Input() tooltipIcon: string;
  @Input() tooltipText: string;
  /**
   * Popover icon and additional content for it.
   */
  @Input() popoverIcon: string;
  @Input() popoverPlacement: string = 'top';
  @Input() popoverTriggers: string = 'click';
  @Input() popoverContent: string | TemplateRef<any>;
  @Input() labelCount: boolean;

  /**
   * Attached to the aria-label attribute of the host element. In most cases, arial-labelledby will
   * take precedence so this may be omitted.
   */
  @Input('aria-label') ariaLabel: string | null = null;

  /**
   * Users can specify the `aria-labelledby` attribute which will be forwarded to the input element
   */
  @Input('aria-labelledby') ariaLabelledby: string | null = null;

  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('aria-describedby') ariaDescribedBy: string | null = null;

  // eslint-disable-next-line @angular-eslint/no-output-native, @angular-eslint/no-output-rename
  @Output('focus')
  readonly focusEmitter: EventEmitter<FocusEvent> = new EventEmitter<FocusEvent>();
  // eslint-disable-next-line @angular-eslint/no-output-native, @angular-eslint/no-output-rename
  @Output('blur')
  readonly blurEmitter: EventEmitter<FocusEvent> = new EventEmitter<FocusEvent>();

  /** Whether the element is required. */
  @Input()
  get required(): any {
    return this._required;
  }

  set required(value: any) {
    this._required = toBoolean(value);
  }

  /** Whether the element is readonly. */
  @Input()
  get readonly(): any {
    return this._readonly;
  }

  set readonly(value: any) {
    this._readonly = toBoolean(value);
  }

  /**
   * Use disabled style for the control if [readonly]=true
   */
  @Input() disabledStyleForReadonlyState: boolean = false;

  /** Whether the element is textarea. */
  @Input()
  get rows(): any {
    return this._rows;
  }

  set rows(value: any) {
    this._rows = toNumber(value);
  }

  /** Whether the element has maxrows. */
  @Input()
  get maxrows(): any {
    return this._maxrows;
  }

  set maxrows(value: any) {
    this._maxrows = toNumber(value);
  }

  /** Whether the element has autofocus. */
  @Input()
  get autofocus(): any {
    return this._autofocus;
  }

  set autofocus(value: any) {
    this._autofocus = toBoolean(value);
  }

  /** The input element's value. */
  get value(): any {
    return this._value;
  }

  set value(v: any) {
    if (v !== this._value) {
      this._value = v;
      this.onChangeCallback(v);
    }
  }

  @Input()
  set toggleStateValidation(value: any) {
    if (this.control) {
      if (value) {
        this.control.markAsDirty();
        this.control.markAsTouched();
      } else {
        this.control.markAsPristine();
        this.control.markAsUntouched();
      }
    }
  }

  private _readonly: boolean = false;
  private _rows: number = null;
  private _maxrows: number = -1;
  private _autofocus: boolean = false;
  private _value: string;
  private hasFocus: boolean = false;
  private onTouchedCallback: () => void = noop;
  private onChangeCallback: (_: any) => void = noop;
  private _destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);

  /**
   * Private variables of textfield component
   */
  private _required: boolean = false;

  constructor(private elemRef: ElementRef,
              private renderer: Renderer2,
              private _dir: Directionality,
              private _ngZone: NgZone,
              private _resizeService: ResizeService,
              _adaptRadarService: AdaptRadarService) {
    super(_adaptRadarService, elemRef, AdaptRadarAngularCustomEventName.AdaptTextFieldFormBuilder);

    this.renderer = renderer;
    this.isRtl = _dir.value === 'rtl';
  }

  /**
   * interface ControlValueAccessor
   * writeValue(obj: any): void
   */
  writeValue(value: any): void {
    if (this._value !== value) {
      this._value = value;
      this._applyTextOverflow();
    }
  }

  /**
   * interface ControlValueAccessor
   * registerOnChange(fn: any): void
   */
  registerOnChange(fn: any): void {
    this.onChangeCallback = fn;
  }

  /**
   * interface ControlValueAccessor
   * registerOnTouched(fn: any): void
   */
  registerOnTouched(fn: any): void {
    this.onTouchedCallback = fn;
  }

  ngOnInit(): void {
    this.nestedSettings = {...this.nestedSettingsDefault, ...this.nestedSettings};

    if (this.tooltipIcon && !this.popoverIcon) {
      this.tooltipConfig = {
        iconName: this.tooltipIcon,
        content: this.tooltipText,
        placement: 'top',
        popoverMode: false
      };
    } else if (this.popoverIcon) {
      this.tooltipConfig = {
        iconName: this.popoverIcon,
        content: this.popoverContent,
        placement: this.popoverPlacement,
        popoverMode: true,
        popoverTriggers: this.popoverTriggers
      };
    }
  }

  ngAfterViewInit(): void {
    super.ngAfterViewInit();

    if (this.inputElement && this.overflowText) {
      this._ngZone.onStable
        .pipe(take(1))
        .subscribe(() => {
          this.inputStyles = {...getElementStyles(this.inputElement.nativeElement)};
        });

      this._resizeService.addResizeEventListener(this.elemRef.nativeElement, debounce(this._applyTextOverflow.bind(this), 100));

      fromEvent(this.inputElement.nativeElement, 'input')
        .pipe(
          debounceTime(300),
          distinctUntilChanged(),
          takeUntil(this._destroyed$)
        )
        .subscribe(() => {
          this._applyTextOverflow();
        });
    }
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();

    this._resizeService.removeResizeEventListener(this.elemRef.nativeElement);
    this._destroyed$.next(true);
    this._destroyed$.complete();
  }

  /**
   * Control disabled state from component
   */
  ngOnChanges(changes: SimpleChanges): void {
    const disableControl: SimpleChange = changes.disableControl;

    if (disableControl) {
      disableControl.currentValue ? this.control.disable() : this.control.enable();
    }
  }

  /**
   * Callback for the cases where the focused state of the input changes
   */
  onFocus(event: any): void {
    this.hasFocus = true;
    this.renderer.addClass(this.elemRef.nativeElement, cssTextFieldClasses.FOCUS);
    this.focusEmitter.emit(event);
  }

  /**
   * Callback for the cases where the focused state is out of input element
   */
  onBlur(event: any): void {
    this.hasFocus = false;
    this.renderer.addClass(this.elemRef.nativeElement, cssTextFieldClasses.FOCUS);
    this.onTouchedCallback();
    this.blurEmitter.emit(event);
  }

  onKeyup(): void {
    this.value = this.inputElement.nativeElement.value;
  }

  private _applyTextOverflow(): void {
    if (this.overflowText && this.inputTextWidthEl && this.inputElement) {
      const div = this.inputTextWidthEl.nativeElement;
      const input = this.inputElement.nativeElement as HTMLInputElement;

      this.isTextOverflow = div.clientWidth > input.clientWidth;
    }
  }
}
