import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  Renderer2,
  ViewEncapsulation
} from '@angular/core';

import {
  AdaptRadarAngularCustomEventName,
  AdaptTranslateService,
  KeyValueObject
} from '@bmc-ux/adapt-angular';
import {AdaptRadarGenericDirective, AdaptRadarService} from '@bmc-ux/adapt-radar';

const RATING_BINARY_TYPE = ['icon', 'text', 'count'];

export class RatingBinaryTypeError extends Error {
  constructor(type: string) {
    super(`Rating binary type "${type}" isn't supported (allowed: 'icon', 'text', 'count').`);
  }
}

@Component({
  selector: 'adapt-rating-binary',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.a-rating-binary]': 'true'
  },
  templateUrl: './rating-binary.component.html',
  encapsulation: ViewEncapsulation.None,
  styles: [`
    /* Hot fix for IE 11 */
    .a-rating-center::after {
      content: '';
      display: inline-block;
      min-height: inherit;
      font-size: 0;
    }
  `]
})

export class AdaptRatingBinaryComponent extends AdaptRadarGenericDirective implements OnChanges {
  toggle: boolean = true;
  showMessage: string;
  showBtnValue: string;
  savedValue: number;
  quotesLeft: string;
  quotesRight: string;

  // TODO: change names

  @Input() btnTextLeft: string; // 'Yes'
  @Input() btnTextRight: string; // 'No';
  @Input() undoButton: string; // 'Undo';

  // eslint-disable-next-line @angular-eslint/no-output-on-prefix, @angular-eslint/no-output-rename
  @Output('change-value') readonly onChangeValue: EventEmitter<{ value: string | number }> = new EventEmitter();

  @Input()
  get type(): 'icon' | 'text' | 'count' {
    return this._type;
  }

  set type(t: 'icon' | 'text' | 'count') {
    this._type = t;
  }

  @Input()
  get likeMessage(): string {
    return this._likeMessage;
  }

  set likeMessage(message: string) {
    this._likeMessage = message;
  }

  @Input()
  get dislikeMessage(): string {
    return this._dislikeMessage;
  }

  set dislikeMessage(message: string) {
    this._dislikeMessage = message;
  }

  @Input()
  get textMessage(): string {
    return this._textMessage;
  }

  set textMessage(v: string) {
    this._textMessage = v;
  }

  @Input()
  get value(): number {
    return this._value;
  }

  set value(v: number) {
    this._value = v;
  }

  private _type: 'icon' | 'text' | 'count' = 'text';
  private _likeMessage: string; // 'You liked the article. ';
  private _dislikeMessage: string; // 'You disliked the article. ';
  private _textMessage: string; // 'You replied';
  private _value: number = 12;

  constructor(private _element: ElementRef,
              private _renderer: Renderer2,
              private ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarService) {
    super(_adaptRadarService, _element, AdaptRadarAngularCustomEventName.AdaptRatingBinary);

    this.setDefaultTexts(ts.getCurrentLanguage());
  }

  setDefaultTexts(translation: KeyValueObject): void {
    this.btnTextLeft = translation['adapt.rating.binary.left'];
    this.btnTextRight = translation['adapt.rating.binary.right'];
    this.undoButton = translation['adapt.common.undo'];
    this.quotesLeft = translation['adapt.common.doubleQuotesLeft'];
    this.quotesRight = translation['adapt.common.doubleQuotesRight'];
    this._likeMessage = translation['adapt.rating.binary.like'];
    this._dislikeMessage = translation['adapt.rating.binary.dislike'];
    this._textMessage = translation['adapt.rating.binary.confirm'];
  }

  handleClick(e: Event, type: string = ''): void {
    e.preventDefault();
    this._setHostStyles();
    this._toggleState();

    switch (type) {
      case 'like':
        this.showMessage = this.likeMessage;
        this.showBtnValue = this.btnTextLeft;
        this.onChangeValue.emit({value: this.btnTextLeft});
        break;
      case 'dislike':
        this.showMessage = this.dislikeMessage;
        this.showBtnValue = this.btnTextRight;
        this.onChangeValue.emit({value: this.btnTextRight});
        break;
      default:
        this.onChangeValue.emit({value: this.undoButton});
        break;
    }
  }

  changeValue(action: string): void {
    this._setHostStyles();
    this._toggleState();

    switch (action) {
      case 'increase':
        this.savedValue = this.value;
        this.value++;
        this.onChangeValue.emit({value: this.value});
        break;
      case 'decrease':
        this.savedValue = this.value;
        this.value--;
        this.onChangeValue.emit({value: this.value});
        break;
    }
  }

  cancelValue(e: MouseEvent): void {
    e.preventDefault();
    this.reset();
  }

  reset(): void {
    this.value = this.savedValue;
    this.onChangeValue.emit({value: this.undoButton});
    this._toggleState();
  }

  ngOnChanges(): void {
    if (this.type && RATING_BINARY_TYPE.indexOf(this.type) === -1) {
      throw new RatingBinaryTypeError(this.type);
    }
  }

  private _setHostStyles(): void {
    if (this._element) {
      const {nativeElement} = this._element;
      const {offsetHeight} = nativeElement;

      this._renderer.setStyle(nativeElement, 'minHeight', `${offsetHeight}px`);
      this._renderer.addClass(nativeElement, 'a-rating-center');
    }
  }

  private _toggleState(): void {
    this.toggle = !this.toggle;
  }

}
