import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  OnChanges,
  Output,
  QueryList,
  Renderer2,
  SimpleChanges,
  TemplateRef,
  ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import {transition, trigger, useAnimation} from '@angular/animations';
import {take} from 'rxjs/operators';
import {
  AdaptRadarAngularCustomEventName,
  AdaptTranslateService,
  ANIMATION_DURATION,
  ANIMATION_NAME,
  toNumber,
  TooltipPlacement
} from '@bmc-ux/adapt-angular';
import {AdaptRadarGenericDirective, AdaptRadarService} from '@bmc-ux/adapt-radar';

@Component({
  selector: 'adapt-time-slot',
  templateUrl: './time-slot.component.html',
  encapsulation: ViewEncapsulation.None,
  animations: [
    trigger('slotsAnimation', [
      transition('void => *', [
        useAnimation(ANIMATION_NAME.opacity, {
          params: {
            duration: ANIMATION_DURATION.medium
          }
        })
      ])
    ])
  ]
})

export class AdaptTimeSlotComponent extends AdaptRadarGenericDirective implements AfterViewInit, OnChanges {
  /**
   * Tooltip icon and additional text for it.
   */
  @Input() tooltipIcon: string;
  @Input() tooltipText: string;
  /**
   * Popover icon and additional content for it.
   */
  @Input() popoverIcon: string;
  @Input() popoverPlacement: TooltipPlacement = 'top';
  @Input() popoverTriggers: string = 'click';
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() popoverContent: string | TemplateRef<any>;

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any, @angular-eslint/no-output-rename */
  @Output('selected-slot-changed') readonly selectedSlotChanged: EventEmitter<{ index: any; slot: any }> = new EventEmitter();

  @ViewChildren('button') button: QueryList<ElementRef>;

  @Input()
  get label(): string {
    return this._label;
  }

  set label(v: string) {
    this._label = v;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() get slot(): any[] {
    return this._slot;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  set slot(v: any[]) {
    this._slot = v;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() get selectedSlot(): any {
    return this._selectedSlot;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  set selectedSlot(slot: any) {
    this._selectedSlot = toNumber(slot);
  }

  private _label: string;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private _slot: any[] = [];
  private _selectedSlot: number = null;

  constructor(private _renderer: Renderer2,
              private ts: AdaptTranslateService,
              private _ngZone: NgZone,
              _adaptRadarService: AdaptRadarService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptTimeSlot);

    const translation = ts.getCurrentLanguage();

    this._label = translation['adapt.timeSlot.available'];
  }

  isSelected(index: number): void {
    if (this.selectedSlot === index) {
      this.selectedSlot = null;
      this.selectedSlotChanged.emit({index: null, slot: null});
    } else {
      this.selectedSlot = index;
      this.selectedSlotChanged.emit({index: this.selectedSlot, slot: this.slot[this.selectedSlot]});
    }
  }

  ngAfterViewInit(): void {
    super.ngAfterViewInit();

    this._setSlotMaxWidth();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.slot) {
      this._ngZone.onStable
        .pipe(take(1))
        .subscribe(() => {
          this._setSlotMaxWidth();
        });
    }
  }

  private _setSlotMaxWidth(): void {
    const btn = this.button.toArray();

    if (btn) {
      const arr = btn.map(item => item.nativeElement.clientWidth);
      const max = Math.round(Math.max(...arr));

      btn.map(_ => this._renderer.setStyle(_.nativeElement, 'width', `${max}px`));
    }
  }
}
