// TODO update types
/* eslint-disable @typescript-eslint/no-explicit-any */

import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Injector,
  Input,
  OnInit, Optional,
  Output,
  Renderer2,
  ViewEncapsulation
} from '@angular/core';
import {transition, trigger, useAnimation} from '@angular/animations';

import {
  AdaptRadarAngularCustomEventName,
  AdaptTranslateService,
  ANIMATION_NAME,
  createFormControlValidator,
  createFormControlValueAccessor,
  FormComponent,
  SafeCdr,
  toBoolean
} from '@bmc-ux/adapt-angular';
import {AdaptRadarService} from '@bmc-ux/adapt-radar';


const noop = (): void => {
  return;
};

let nextUniqueId = 0;

@Component({
  selector: 'adapt-checkbox2, div[adapt-checkbox2], adapt-checkbox',
  templateUrl: './checkbox2.component.html',
  providers: [
    createFormControlValueAccessor(AdaptCheckbox2Component),
    createFormControlValidator(AdaptCheckbox2Component)
  ],
  styles: ['.checkbox__item .adapt-icon { bottom: 0; }'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [
    trigger('validationFeedbackAnimation', [
      transition(':enter', [
        useAnimation(ANIMATION_NAME.fadeIn)
      ]),
      transition(':leave', [
        useAnimation(ANIMATION_NAME.fadeOut)
      ])
    ])
  ]
})
export class AdaptCheckbox2Component extends FormComponent implements OnInit {
  context: AdaptCheckbox2Component = this;
  /**
   * The host element of the component
   */
  _el: HTMLElement;
  /**
   * An unique id for each checkbox/switcher
   */
  _uniqueId: string = `adapt-checkbox-${++nextUniqueId}`;
  _label: string = '';

  @Input() class: string = ''; // override the standard class attr with a new one
  /** A unique id for the checkbox input. If none is supplied, it will be auto-generated. */
  @Input() id: string = this._uniqueId;
  /** Name value will be applied to the input element if present */
  @Input() name: string | null = null;
  /** The tabindex attribute of the native input element */
  @Input() tabindex: number = null;
  @Input() stopPropagation: boolean = true;
  /** The value attribute of the native input element */
  @Input() value: string;
  /**
   * Attached to the aria-label attribute of the host element. In most cases, arial-labelledby will
   * take precedence so this may be omitted.
   */
    // eslint-disable-next-line @typescript-eslint/typedef
  @Input('aria-label') ariaLabel = '';
  /**
   * Users can specify the `aria-labelledby` attribute which will be forwarded to the input element
   */
  @Input('aria-labelledby') ariaLabelledby: string | null = null;
  /**
   * Control [aria-describedby] attribute text
   */
  @Input() ariaDescribedBy: string = null;
  /** the displayMessage attribute allows to show the additional info for the user */
  @Input() displayMessage: string = 'Please fill out this field';
  /** the toggleMessage attribute will toggle the additional messages */
  @Input() toggleMessage: boolean = false;

  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly focus: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly blur: EventEmitter<any> = new EventEmitter();
  /** Event emitted when the switcher value changes. */
    // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly change: EventEmitter<boolean> = new EventEmitter<boolean>();
  /** Event emitted when the checkbox's `indeterminate` value changes. */
  @Output() readonly indeterminateChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  private _onTouchedCallback: () => void = noop;
  private _onChangeCallback: (_: any) => void = noop;

  @HostBinding('class')
  get hostClasses(): string {
    return [
      this.class, // include our new one
      this.mainCls,
      'checkbox',
      this.readonly ? 'readonly' : '',
    ].join(' ');
  }

  /** The input/switcher element's value. */
  get checked(): boolean {
    return this._checked;
  }

  set checked(checked: boolean) {
    this._updateCheckedValue(checked, false);
  }

  @Input()
  get indeterminate(): boolean {
    return this._indeterminate;
  }

  set indeterminate(indeterminate: boolean) {
    const changed = indeterminate !== this._indeterminate;

    this._indeterminate = indeterminate;

    if (changed) {
      this.indeterminateChange.emit(this._indeterminate);
    }
  }

  get inputId(): string {
    return `${this.id || this._uniqueId}-input`;
  }

  get nativeElement(): HTMLElement {
    return this._element.nativeElement;
  }

  /**
   * The label text of the checkbox component
   */
  @Input()
  get label(): string {
    return this._label;
  }

  set label(v: string) {
    this._label = v;
  }

  /**
   * Private variables of checkbox/switcher component
   */

  private _checked: boolean = false;
  /**
   * An indeterminate state of checkbox component - by default is false;
   */
  private _indeterminate: boolean = false;

  constructor(injector: Injector,
              private _element: ElementRef,
              private _renderer: Renderer2,
              private _changeDetectorRef: ChangeDetectorRef,
              ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarService,
              @Optional() _entityName: AdaptRadarAngularCustomEventName) {
    super(injector, ts, _adaptRadarService, _element, _entityName ? _entityName : AdaptRadarAngularCustomEventName.AdaptCheckbox2);
    this._el = _element.nativeElement;
  }

  ngOnInit(): void {
    super.ngOnInit();
  }

  validate(): null {
    return null;
  }

  /**
   *
   * @returns "true" | "false" | "mixed"
   */
  _getAriaChecked(): 'true' | 'false' | 'mixed' {
    return this.checked ? 'true' : (this.indeterminate ? 'mixed' : 'false');
  }

  /**
   * interface ControlValueAccessor
   * writeValue(obj: any): void
   */
  writeValue(checked: any): void {
    this._updateCheckedValue(!!checked, true);
  }

  /**
   * interface ControlValueAccessor
   * registerOnChange(fn: any): void
   */
  registerOnChange(fn: any): void {
    this._onChangeCallback = fn;
  }

  /**
   * interface ControlValueAccessor
   * registerOnTouched(fn: any): void
   */
  registerOnTouched(fn: any): void {
    this._onTouchedCallback = fn;
  }

  /**
   * interface ControlValueAccessor
   * setDisabledState(isDisabled: boolean)?: void
   */
  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    SafeCdr.markForCheck(this._changeDetectorRef);
  }

  /**
   * Set focus state of the input changes
   */
  onFocus(): void {
    this._renderer.addClass(this._el, 'is-focused');
  }

  /**
   * Remove focus state of the input changes
   */
  onBlur(): void {
    this._renderer.removeClass(this._el, 'is-focused');
    this._onTouchedCallback();
  }

  /**
   * Toggle the state of input element on user action
   */
  onInputClick(e: Event): void {
    if (this.stopPropagation) {
      e.stopPropagation();
    }

    if (!(this.disabled || this.readonly)) {
      if (this.indeterminate) {
        this.indeterminate = false;
        this.indeterminateChange.emit(this.indeterminate);
      }
      this._toggle();
    } else {
      e.preventDefault();
    }
  }

  onInteractionEvent(e: Event): void {
    // We always have to stop propagation on the change event.
    // Otherwise the change event, from the input element, will bubble up and
    // emit its event object to the `change` output.
    e.stopPropagation();
  }


  /**
   * Toggle checked/unchecked states in checkbox/switcher component
   */
  private _toggle(): void {
    this.checked = !this.checked;
  }

  private _updateCheckedValue(checked: boolean, fromOuterComponent: boolean): void {
    if (checked !== this.checked) {
      this._checked = toBoolean(checked);

      if (!fromOuterComponent) {
        this.change.emit(checked);
      }
      this._onChangeCallback(checked);
      SafeCdr.markForCheck(this._changeDetectorRef);
    }
  }
}
