/* eslint-disable @typescript-eslint/no-explicit-any */

import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Injector,
  Input,
  OnInit,
  Output,
  Renderer2,
  ViewEncapsulation
} from '@angular/core';
import {
  AdaptRadarAngularCustomEventName,
  AdaptTranslateService,
  createFormControlValidator,
  createFormControlValueAccessor
} from '@bmc-ux/adapt-angular';
import {AdaptCheckbox2Component} from '../checkbox2/index';
import {AdaptRadarService} from '@bmc-ux/adapt-radar';


let nextUniqueId = 0;

@Component({
  selector: 'adapt-switcher, div[adapt-switcher]',
  providers: [
    createFormControlValueAccessor(AdaptSwitcherComponent),
    createFormControlValidator(AdaptSwitcherComponent),
  ],
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.is-checked]': 'context.checked',
    '[class.is-disabled]': 'context.disabled'
  },
  templateUrl: './switcher.component.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AdaptSwitcherComponent extends AdaptCheckbox2Component implements OnInit {
  context: AdaptSwitcherComponent = this;
  _label: string = '';
  _uniqueId: string = `adapt-checkbox-${++nextUniqueId}`;

  @Input() labelPosition: 'before' | 'after' = 'after';

  /**
   * sizes - lg and sm
   */
  @Input() size: string = '';

  @Input() @HostBinding('class')
  get hostClasses(): string {
    return [
      'switcher',
      this.labelPosition === 'before' ? 'switcher__before' : ''
    ].join(' ');
  }


  /** A unique id for the checkbox input. If none is supplied, it will be auto-generated. */
  @Input() id: string = this._uniqueId;

  get inputId(): string {
    return `${this.id || this._uniqueId}-input`;
  }


  /** Name value will be applied to the input element if present */
  @Input() name: string | null = null;

  /** The tabindex attribute of the native input element */
  @Input() tabindex: number = null;

  /**
   * The label text of the checkbox component
   */
  @Input()
  get label(): string {
    return this._label;
  }

  set label(v: string) {
    this._label = v;
  }

  /** The value attribute of the native input element */
  @Input() value: string;

  /**
   * Attached to the aria-label attribute of the host element. In most cases, arial-labelledby will
   * take precedence so this may be omitted.
   */
  @Input('aria-label') ariaLabel: string = '';

  /**
   * Users can specify the `aria-labelledby` attribute which will be forwarded to the input element
   */
  @Input('aria-labelledby') ariaLabelledby: string | null = null;

  @Input() ariaDescribedBy: string | null = null;

  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly focus: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly blur: EventEmitter<any> = new EventEmitter();

  /** Event emitted when the switcher value changes. */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly change: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor(
    injector: Injector,
    element: ElementRef,
    renderer: Renderer2,
    changeDetectorRef: ChangeDetectorRef,
    ts: AdaptTranslateService,
    _adaptRadarService: AdaptRadarService) {
    super(injector, element, renderer, changeDetectorRef, ts, _adaptRadarService, AdaptRadarAngularCustomEventName.AdaptSwitcher);
  }

  ngOnInit(): void {
    super.ngOnInit();
  }

  onInputClick(e: any): void {
    super.onInputClick(e);
  }
}
