import {ModuleWithProviders, NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule} from '@angular/forms';
import {DragDropModule} from '@angular/cdk/drag-drop';
import {ai_dots} from '@bmc-ux/dpl-iconfont';

import {AdaptListComponent} from './list-selector.component';
import {
  AdaptEmptyStateModule,
  AdaptIconModule,
  AdaptTooltipModule,
  AdaptPopoverModule,
  AdaptRxLabelModule,
  AdaptIconConfig
} from '@bmc-ux/adapt-angular';
import {AdaptSelectModule} from '../select/index';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    DragDropModule,
    AdaptSelectModule,
    AdaptEmptyStateModule,
    AdaptIconModule,
    AdaptTooltipModule,
    AdaptPopoverModule,
    AdaptRxLabelModule
  ],
  declarations: [AdaptListComponent],
  exports: [AdaptListComponent]
})
export class AdaptListSelectorModule {
  static forRoot(): ModuleWithProviders<AdaptListSelectorModule> {
    return {
      ngModule: AdaptListSelectorModule
    };
  }

  constructor(iconService: AdaptIconConfig) {
    iconService.registerIcons([ai_dots]);
  }
}

export * from './list-selector.component';
export * from './list-selector.model';
