import {
  Component,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef,
  Renderer2,
  TemplateRef,
  Input,
  OnDestroy
} from '@angular/core';
import {Subject} from 'rxjs';
import {Directionality} from '@angular/cdk/bidi';
import {AdaptDeviceDetectionService, convertPlacement, isString, KeyValueObject, PlacementArray, positionElements} from '@bmc-ux/adapt-angular';
import {MentionStyleConfig} from '../mention.model';


@Component({
  selector: 'adapt-mention-list',
  templateUrl: './mention-list.component.html',
  providers: [AdaptDeviceDetectionService]
})
export class AdaptMentionListComponent implements OnDestroy {
  mentionStyleConfig: MentionStyleConfig = {
    width: '200px',
    maxHeight: '360px'
  };

  items: (string | KeyValueObject)[];
  hidden: boolean = true;
  showMentionLoader: boolean = false;
  activeItem: number = 0;
  searchKey: string;
  itemTemplate: TemplateRef<string | KeyValueObject>;
  topSideTemplate: TemplateRef<(string | KeyValueObject)[]>;
  caretUpdated$: Subject<number> = new Subject();

  inputStyles: CSSStyleDeclaration;

  @Input() set appendToBody(v: boolean) {
    if (Boolean(v) !== this._appendToBody) {
      this._appendToBody = Boolean(v);

      if (this._appendToBody) {
        this._renderer.appendChild(document.body, this.list.nativeElement);
      } else {
        this._renderer.appendChild(this._elementRef.nativeElement, this.list.nativeElement);
      }
    }
  }

  get appendToBody(): boolean {
    return this._appendToBody;
  }

  @ViewChild('list', {static: true}) list: ElementRef<HTMLDivElement>;
  @ViewChild('caretPositionDiv', {static: true}) caretPositionDiv: ElementRef<HTMLDivElement>;

  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onItemClick: EventEmitter<number> = new EventEmitter();

  private _isMobile: boolean = false;
  private _isRtl: boolean = false;
  private _inputEl: HTMLInputElement | HTMLTextAreaElement;
  private _appendToBody: boolean = false;

  constructor(private _deviceDetection: AdaptDeviceDetectionService,
              private _renderer: Renderer2,
              private _elementRef: ElementRef,
              private _dir: Directionality) {
  }

  ngOnDestroy(): void {
    if (this.appendToBody) {
      this._renderer.removeChild(document.body, this.list.nativeElement);
    }
  }

  initializeConfig(inputEl: HTMLInputElement | HTMLTextAreaElement, mentionStyleConfig: MentionStyleConfig): void {
    this.inputStyles = {...this._getElementStyles(inputEl)};
    this._isMobile = this._deviceDetection.isMobile();
    this._isRtl = this._dir.value === 'rtl';
    this._inputEl = inputEl;

    if (mentionStyleConfig) {
      this.mentionStyleConfig = mentionStyleConfig;

      if (this._isMobile) {
        this.mentionStyleConfig.width = '100%';
      }
    }

    this.caretUpdated$.subscribe(index => {
      this.activeItem = 0;

      this._setListPosition(this._inputEl, index);
    });
  }

  onClick(event: Event, index: number): void {
    event.preventDefault();
    this.activeItem = index;
    this.onItemClick.emit(index);
  }

  focusNext(): void {
    if (this.items.length - 1 !== this.activeItem) {
      this.activeItem++;
    }
  }

  focusPrev(): void {
    if (this.activeItem !== 0) {
      this.activeItem--;
    }
  }

  trackByIndex(index: number): number {
    return index;
  }

  hideList(): void {
    this.hidden = true;
  }

  showList(): void {
    this.hidden = false;
  }

  getTempalteParams(value: string | KeyValueObject): KeyValueObject {
    return isString(value) ? {'$implicit': value} : {...value};
  }

  setTemplates(itemTemplate: TemplateRef<string | KeyValueObject>, topSideTemplate: TemplateRef<(string | KeyValueObject)[]>): void {
    this.itemTemplate = itemTemplate;
    this.topSideTemplate = topSideTemplate;
  }

  getActiveValue(): string | KeyValueObject {
    return this.items[this.activeItem];
  }

  setLoaderState(state: boolean): void {
    this.showMentionLoader = state;
  }

  setListItems(items: (string | KeyValueObject)[]): void {
    this.items = items;
  }

  setActiveIndex(index: number): void {
    this.activeItem = index;
  }

  setSearchKey(searchKey: string): void {
    this.searchKey = searchKey;
  }

  private _setListPosition(element: HTMLInputElement | HTMLTextAreaElement, caretPosition: number): void {
    this._renderer.setStyle(this.list.nativeElement, 'zIndex', this.appendToBody ? '1070' : '1');
    const ltrPlacement = [
      'bottom-left',
      'bottom',
      'bottom-right',
      'top-left',
      'top',
      'top-right',
      'bottom-left',
      'bottom',
      'bottom-right',
    ];
    const placement: PlacementArray = this._isRtl ? convertPlacement(ltrPlacement) : ltrPlacement;

    positionElements(this._getCaretPositionElement(element, caretPosition), this.list.nativeElement, placement, this.appendToBody);
  }

  private _getElementStyles(element: HTMLInputElement | HTMLTextAreaElement): CSSStyleDeclaration {
    const isFirefox = this._deviceDetection.isFirefox();
    const isInput = element.nodeName === 'INPUT';
    const computed = window.getComputedStyle(element);

    const properties = [
      'direction',  // RTL support
      'boxSizing',
      'width',  // on Chrome and IE, exclude the scrollbar, so the mirror div wraps exactly as the textarea does
      'height',
      'overflowX',
      'overflowY',  // copy the scrollbar for IE

      'borderTopWidth',
      'borderRightWidth',
      'borderBottomWidth',
      'borderLeftWidth',
      'borderStyle',

      'paddingTop',
      'paddingRight',
      'paddingBottom',
      'paddingLeft',

      // https://developer.mozilla.org/en-US/docs/Web/CSS/font
      'fontStyle',
      'fontVariant',
      'fontWeight',
      'fontStretch',
      'fontSize',
      'fontSizeAdjust',
      'lineHeight',
      'fontFamily',

      'textAlign',
      'textTransform',
      'textIndent',
      'textDecoration',  // might not make a difference, but better be safe

      'letterSpacing',
      'wordSpacing',

      'tabSize',
    ];

    const div = this.caretPositionDiv.nativeElement;

    return this._calculateElementStyles(div, properties, computed, isInput, isFirefox);
  }

  private _getCaretPositionElement(element: HTMLInputElement | HTMLTextAreaElement, position: number): HTMLElement {
    const isInput = element.nodeName === 'INPUT';
    const div = this.caretPositionDiv.nativeElement;

    div.textContent = element.value.substring(0, position);

    // The second special handling for input type="text" vs textarea:
    // spaces need to be replaced with non-breaking spaces - http://stackoverflow.com/a/13402035/1269037
    if (isInput) {
      div.textContent = div.textContent.replace(/\s/g, '\u00a0');
    }

    // Create span which will be used for the mention list positioning
    const caretSpan = this._renderer.createElement('span');

    caretSpan.textContent = '';
    this._renderer.appendChild(div, caretSpan);

    // Wrapping must be replicated *exactly*, including when a long word gets
    // onto the next line, with whitespace at the end of the line before.
    // The  *only* reliable way to do that is to copy the *entire* rest of the
    // textarea's content into the <span> created at the caret position.
    const restContentSpan = this._renderer.createElement('span');

    restContentSpan.textContent = element.value.substring(position);
    this._renderer.appendChild(div, restContentSpan);

    // INPUT: Apply the same scrollLeft for case if scroll exist
    div.scrollLeft = element.scrollLeft + caretSpan.offsetWidth + parseInt(div.style.paddingRight, 10);

    // TEXTAREA: Apply the same scrollTop for case if scroll exist
    div.scrollTop = element.scrollTop;

    return caretSpan;
  }

  private _calculateElementStyles(element: HTMLDivElement, properties: string[], computedStyles: CSSStyleDeclaration,
                                  isInput: boolean, isFirefox: boolean): CSSStyleDeclaration {
    const styles = {} as CSSStyleDeclaration;

    if (isInput) {
      styles.whiteSpace = 'nowrap';
    } else {
      // Default textarea styles
      styles.whiteSpace = 'pre-wrap';
      styles.wordWrap = 'break-word';
    }

    // Position off-screen
    styles.position = 'absolute';  // required to return coordinates properly
    styles.top = '0';  // need to place it at the same position as input element
    styles.visibility = 'hidden';  // not 'display: none' because we want rendering

    // Transfer the element's properties to the div
    properties.forEach((prop) => {
      styles[prop] = computedStyles[prop];
    });

    if (isFirefox) {
      // Firefox lies about the overflow property for textareas: https://bugzilla.mozilla.org/show_bug.cgi?id=984275
      if (element.scrollHeight > parseInt(computedStyles.height, 10)) {
        styles.overflowY = 'scroll';
      }
    } else {
      styles.overflow = 'hidden';  // for Chrome to not render a scrollbar; IE keeps overflowY = 'scroll'
    }

    return styles;
  }

}
