import {
  Component, ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  ViewEncapsulation
} from '@angular/core';
import {
  AdaptRadarAngularCustomEventName,
  AdaptTranslateService,
  KeyValueObject,
  TooltipPlacement
} from '@bmc-ux/adapt-angular';
import {AdaptRadarGenericDirective, AdaptRadarService} from '@bmc-ux/adapt-radar';

const RATING_VOTING_TYPES = [
  'star',
  'flag',
  'like',
  'thanks',
];

export type AdaptRatingType = 'star' | 'flag' | 'like' | 'thanks';

export class RatingVotingTypeError extends Error {
  constructor(type: string) {
    super(`Rating voting type "${type}" isn't supported (allowed: star, like, flag, thanks).`);
  }
}

@Component({
  selector: 'adapt-rating-voting',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.a-rating-voting]': 'true'
  },
  templateUrl: './rating-voting.component.html',
  encapsulation: ViewEncapsulation.None
})

export class AdaptRatingVotingComponent extends AdaptRadarGenericDirective implements OnChanges {
  isActive: boolean = false;
  wasClick: boolean = true;

  @Input() type: AdaptRatingType = 'star';

  // eslint-disable-next-line @angular-eslint/no-output-on-prefix, @angular-eslint/no-output-rename
  @Output('voting-change') readonly onVotingChanged: EventEmitter<{ type: AdaptRatingType; isActive: boolean }> = new EventEmitter();

  @Input()
  get thanksText(): string {
    return this._thanksText;
  }

  set thanksText(text: string) {
    this._thanksText = text;
  }

  @Input()
  get thanksAbbrMessage(): string {
    return this._thanksAbbrMessage;
  }

  set thanksAbbrMessage(text: string) {
    this._thanksAbbrMessage = text;
  }

  @Input()
  get tooltip(): string {
    return this._tooltip;
  }

  set tooltip(v: string) {
    this._tooltip = v;
  }

  @Input()
  get tooltipPlacement(): TooltipPlacement {
    return this._tooltipPlacement;
  }

  set tooltipPlacement(placement: TooltipPlacement) {
    this._tooltipPlacement = placement;
  }

  private _thanksText: string;
  private _thanksAbbrMessage: string;
  private _tooltip: string = '';
   private _tooltipPlacement: TooltipPlacement = 'top';

  constructor(private ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptRatingVoting);

    this.setDefaultTexts(ts.getCurrentLanguage());
  }

  setDefaultTexts(translation: KeyValueObject): void {
    this.thanksText = translation['adapt.rating.vote.thank'];
    this.thanksAbbrMessage = translation['adapt.rating.vote.confirm'];
  }

  handleClick(): void {
    this.isActive = !this.isActive;
    this.onVotingChanged.emit({type: this.type, isActive: this.isActive});

    if (this.type === 'thanks') {
      this.wasClick = false;
    }
  }

  ngOnChanges(): void {
    if (this.type && RATING_VOTING_TYPES.indexOf(this.type) === -1) {
      throw new RatingVotingTypeError(this.type);
    }
  }

}
