/* eslint-disable @typescript-eslint/no-explicit-any */

import {
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Input,
  NgZone,
  OnInit,
  Output,
  QueryList,
  TemplateRef,
  ViewChildren
} from '@angular/core';
import {take} from 'rxjs/operators';

import {toString, scrollToActive} from '@bmc-ux/adapt-angular';

/**
 * Context for the typeahead result template in case you want to override the default one
 */
export interface ResultTemplateContext {
  /**
   * Your typeahead result data model
   */
  result: any;

  /**
   * Search term from the input used to get current result
   */
  term: string;
}

@Component({
  selector: 'ngb-typeahead-window, adapt-typeahead-window',
  exportAs: 'ngbTypeaheadWindow',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {'role': 'listbox', '[id]': 'id'},
  template: `
    <ng-template #rt let-result="result" let-term="term">
      <adapt-highlight [result]="formatter(result)" [term]="term"></adapt-highlight>
    </ng-template>
    <ng-container *ngIf="virtualScroll">
      <cdk-virtual-scroll-viewport
        [itemSize]="virtualScrollItemSize"
        [ngStyle]="{'height': virtualScrollDropdownHeight}">
        <button #optionButton
                *cdkVirtualFor="let result of results; let idx = index; trackBy: trackByForResults"
                type="button"
                class="dropdown-item"
                role="option"
                [id]="id + '-' + idx"
                [class.active]="idx === activeIdx"
                (mouseenter)="markActive(idx)"
                (click)="select(result)">
          <ng-template [ngTemplateOutlet]="resultTemplate || rt"
                       [ngTemplateOutletContext]="{result: result, term: term}"></ng-template>
        </button>
      </cdk-virtual-scroll-viewport>
    </ng-container>
    <ng-container *ngIf="!virtualScroll">
      <button #optionButton
              *ngFor="let result of results; let idx = index; trackBy: trackByForResults"
              type="button"
              class="dropdown-item"
              role="option"
              [id]="id + '-' + idx"
              [class.active]="idx === activeIdx"
              (mouseenter)="markActive(idx)"
              (click)="select(result)">
        <ng-template [ngTemplateOutlet]="resultTemplate || rt"
                     [ngTemplateOutletContext]="{result: result, term: term}"></ng-template>
      </button>
    </ng-container>
  `
})
export class AdaptTypeaheadWindowComponent implements OnInit {
  activeIdx: number = 0;

  /**
   *  The id for the typeahead window. The id should be unique and the same
   *  as the associated typeahead's id.
   */
  @Input() id: string;

  /**
   * Flag indicating if the first row should be active initially
   */
  @Input() focusFirst: boolean = true;

  /**
   * Typeahead match results to be displayed
   */
  @Input() results: any;

  /**
   * Search term used to get current results
   */
  @Input() term: string;

  /**
   * A function used to format a given result before display. This function should return a formatted string without any
   * HTML markup
   */
  @Input() formatter: Function = toString;

  /**
   * A template to override a matching result default display
   */
  @Input() resultTemplate: TemplateRef<ResultTemplateContext>;

  /**
   * The flag informing that the popup is appended to document.body
   */
  @Input() appendedToBody: boolean = false;

  /**
   * Custom class for host element
   */
  @Input() hostClass: string;
  /**
   * Enable virtual scroll for results window. Can be suitable for big amount of results
   */
  @Input() virtualScroll: boolean;
  /**
   * Used to set "itemSize" virtual scroll @Input()
   */
  @Input() virtualScrollItemSize: number;
  /**
   * Used to set width for results dropdown
   */
  @Input() virtualScrollDropdownHeight: string;
  /**
   * Used to set height for results dropdown
   */
  @Input() virtualScrollDropdownWidth: string;
  /**
   * Event raised when user selects a particular result row
   */
  // eslint-disable-next-line @angular-eslint/no-output-native, @angular-eslint/no-output-rename
  @Output('select') readonly selectEvent: EventEmitter<any> = new EventEmitter();

  // eslint-disable-next-line @angular-eslint/no-output-rename
  @Output('activeChange') readonly activeChangeEvent: EventEmitter<any> = new EventEmitter();

  @ViewChildren('optionButton') optionButtons: QueryList<ElementRef<HTMLButtonElement>>;

  @HostBinding('class') get className(): string {
    return `dropdown-menu show ${this.hostClass || ''} ${this.virtualScroll ? 'dropdown-menu_virtual-scroll' : ''}`;
  }

  @HostBinding('style.width') get dropdownWidth(): string {
    return this.virtualScroll ? this.virtualScrollDropdownWidth : 'auto';
  }

  @HostBinding('style.zIndex') get popupZIndex(): string {
    return this.appendedToBody ? '1060' : null;
  }

  constructor(private _elementRef: ElementRef<HTMLElement>,
              private _ngZone: NgZone) {
  }

  hasActive(): boolean {
    return this.activeIdx > -1 && this.activeIdx < this.results.length;
  }

  getActive(): any {
    return this.results[this.activeIdx];
  }

  markActive(activeIdx: number): void {
    this.activeIdx = activeIdx;
    this._activeChanged();
  }

  next(): void {
    if (this.activeIdx !== this.results.length - 1) {
      this.activeIdx++;
    }
    this._activeChanged();
  }

  prev(): void {
    if (this.activeIdx < 0) {
      this.activeIdx = 0;
    } else if (this.activeIdx > 0) {
      this.activeIdx--;
    }
    this._activeChanged();
  }

  resetActive(): void {
    this.activeIdx = this.focusFirst ? 0 : -1;
    this._activeChanged();
  }

  select(item: any): void {
    this.selectEvent.emit(item);
  }

  ngOnInit(): void {
    this.resetActive();
  }

  trackByForResults(index: number): number {
    return index;
  }

  private _activeChanged(): void {
    this.activeChangeEvent.emit(this.activeIdx >= 0 ? this.id + '-' + this.activeIdx : undefined);

    this.scrollToActiveItem();
  }

  private scrollToActiveItem(): void {
    this._ngZone.onStable.pipe(take(1)).subscribe(() => {
      if (!this.optionButtons) {
        return;
      }
      const activeButtonElRef = this.optionButtons.find(el => el.nativeElement.classList.contains('active'));

      if (activeButtonElRef) {
        const buttonEl = activeButtonElRef.nativeElement;

        scrollToActive(buttonEl);
      }
    });
  }
}
