/* eslint-disable @typescript-eslint/no-explicit-any */

import {
  ApplicationRef,
  ComponentFactoryResolver,
  ComponentRef,
  Directive,
  ElementRef,
  EventEmitter,
  forwardRef,
  Injector,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  TemplateRef,
  ViewContainerRef
} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';


import {AdaptTypeaheadModel} from './typeahead.config';
import {AdaptTypeaheadWindowComponent, ResultTemplateContext} from './typeahead-window';

import {distinctUntilChanged, map, mergeMap, skipWhile, switchMap, take, tap} from 'rxjs/operators';
import {BehaviorSubject, EMPTY, fromEvent, merge, Observable, of, OperatorFunction, Subject, Subscription} from 'rxjs';
import {Live, PopupService, PlacementArray, LayerService, AdaptDeviceDetectionService, positionElements, getEventKeyCode, isDefined, SafeCdr, toString} from '@bmc-ux/adapt-angular';

enum Key {
  Tab = 9,
  Enter = 13,
  Escape = 27,
  ArrowUp = 38,
  ArrowDown = 40
}

const NGB_TYPEAHEAD_VALUE_ACCESSOR = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => AdaptTypeaheadDirective),
  multi: true
};

/**
 * Payload of the selectItem event.
 */
export interface AdaptTypeaheadSelectItemEvent {
  /**
   * An item about to be selected
   */
  item: any;

  /**
   * Function that will prevent item selection if called
   */
  preventDefault: () => void;
}

/**
 * Payload of the typeAheadWindowOpen event.
 */
export interface AdaptTypeAheadPopupEvent {
  /**
   * Text in the input box when event takes place
   */
  text: string;

  /**
   * State of typeAhead pop up, open or closed
   */
  open: boolean;
}

let nextWindowId = 0;

/**
 * NgbTypeahead directive provides a simple way of creating powerful typeaheads from any text input
 */
@Directive({
  selector: '[adaptTypeaheadDirective], input[ngbTypeahead]',
  exportAs: 'ngbTypeahead',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '(blur)': 'handleBlur($event)',
    '[class.open]': 'isPopupOpen()',
    '(keydown)': 'handleKeyDown($event)',
    '(focus)': 'handleFocus($event)',
    '[autocomplete]': 'autocomplete',
    'autocapitalize': 'off',
    'autocorrect': 'off',
    'role': 'combobox',
    '[attr.aria-autocomplete]': 'showHint ? "both" : "list"',
    '[attr.aria-activedescendant]': 'activeDescendant',
    '[attr.aria-owns]': 'isPopupOpen() ? popupId : null',
    '[attr.aria-expanded]': 'isPopupOpen()',
    '[attr.aria-haspopup]': '"listbox"'
  },
  providers: [NGB_TYPEAHEAD_VALUE_ACCESSOR]
})
export class AdaptTypeaheadDirective implements ControlValueAccessor,
  OnInit, OnDestroy {
  /**
   * Value for the configurable autocomplete attribute.
   * Defaults to 'off' to disable the native browser autocomplete, but this standard value does not seem
   * to be always correctly taken into account.
   */
  @Input() autocomplete: string = 'off';
  /**
   * A selector specifying the element the tooltip should be appended to.
   * Currently only supports "body".
   */
  @Input() container: HTMLElement | 'body';
  /**
   * A flag indicating if model values should be restricted to the ones selected from the popup only.
   */
  @Input() editable: boolean;
  /**
   * A flag indicating if the first match should automatically be focused as you type.
   */
  @Input() focusFirst: boolean;
  /**
   * A function to convert a given value into string to display in the input field
   */
  @Input() inputFormatter: (value: any) => string;
  /**
   * A function to transform the provided observable text into the array of results.  Note that the "this" argument
   * is undefined so you need to explicitly bind it to a desired "this" target.
   */
  @Input() ngbTypeahead: OperatorFunction<string, any[]>;
  /**
   * Disables result provisioning while true
   */
  @Input() preventResults: boolean = false;
  /**
   * A function to format a given result before display. This function should return a formatted string without any
   * HTML markup
   */
  @Input() resultFormatter: (value: any) => string;
  /**
   * A template to override a matching result default display
   */
  @Input() resultTemplate: TemplateRef<ResultTemplateContext>;
  /**
   * Show hint when an option in the result list matches.
   */
  @Input() showHint: boolean;
  /**
   * Popup custom class
   */
  @Input() popupClass: string;
  /**
   * Placement of a typeahead accepts:
   * "top", "top-left", "top-right", "bottom", "bottom-left", "bottom-right",
   * "left", "left-top", "left-bottom", "right", "right-top", "right-bottom"
   * and array of above values.
   */
  @Input() placement: PlacementArray = 'auto';

  /**
   * When specified, the drop-down list will be anchored to the selected element
   */
  @Input() positionTo: HTMLElement;
  /**
   * Open popup on focus
   */
  @Input() openOnFocus: boolean = false;
  /**
   * Enable virtual scroll for results window. Can be suitable for big amount of results
   */
  @Input() virtualScroll: boolean;
  /**
   * Used to set "itemSize" virtual scroll @Input()
   */
  @Input() virtualScrollItemSize: number;
  /**
   * Used to set width for results dropdown
   */
  @Input() virtualScrollDropdownWidth: string;
  /**
   * Used to set height for results dropdown
   */
  @Input() virtualScrollDropdownHeight: string;
  /**
   * An event emitted when a match is selected. Event payload is of type NgbTypeaheadSelectItemEvent.
   */
  @Output() readonly selectItem: EventEmitter<AdaptTypeaheadSelectItemEvent> = new EventEmitter<AdaptTypeaheadSelectItemEvent>();
  /**
   * An event emitted when a typeAhead drop down is shown.
   */
  @Output() readonly typeAheadPopupOpen: EventEmitter<AdaptTypeAheadPopupEvent> = new EventEmitter<AdaptTypeAheadPopupEvent>();

  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onBlur: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onFocus: EventEmitter<any> = new EventEmitter();
  activeDescendant: string;
  popupId: string = `ngb-typeahead-${nextWindowId++}`;
  private _popupService: PopupService<AdaptTypeaheadWindowComponent>;
  private _subscription: Subscription;
  private _inputValueBackup: string;
  private _valueChanges: Observable<string>;
  private _resubscribeTypeahead: BehaviorSubject<any>;
  private _windowRef: ComponentRef<AdaptTypeaheadWindowComponent>;
  private _zoneSubscription: any;
  private _setValueSubscription$: any;
  private _openOnFocus$: Subject<string> = new Subject<string>();
  private _documentListenerFunc: () => void;

  private get _activeElement(): Element {
    return document.activeElement;
  }

  private _isIE: boolean = false;

  constructor(private _elementRef: ElementRef<HTMLInputElement>, private _viewContainerRef: ViewContainerRef,
              private _renderer: Renderer2, private _injector: Injector, componentFactoryResolver: ComponentFactoryResolver,
              config: AdaptTypeaheadModel, ngZone: NgZone, private _live: Live, private _layerService: LayerService,
              private _deviceDetection: AdaptDeviceDetectionService,
              private _applicationRef: ApplicationRef) {
    this.container = config.container;
    this.editable = config.editable;
    this.focusFirst = config.focusFirst;
    this.showHint = config.showHint;
    this.placement = config.placement;

    this._valueChanges = fromEvent<Event>(_elementRef.nativeElement, 'input')
      .pipe(map($event => ($event.target as HTMLInputElement).value),
        mergeMap((value) => {
          // IE11 emit input event while placeholder contain spec. caracters
          // fix for the issue https://github.bmc.com/bmc-ux/adapt-angular/issues/2836
          // if it's IE need to check if input is focused, if statement is true - emit event
          if (this._isIE) {
            return this._activeElement === this._elementRef.nativeElement ? of(value) : EMPTY;
          } else {
            return of(value);
          }
        })
      );

    this._resubscribeTypeahead = new BehaviorSubject(null);

    this._popupService = new PopupService<AdaptTypeaheadWindowComponent>(
      AdaptTypeaheadWindowComponent, _injector, _viewContainerRef, _renderer, componentFactoryResolver, _applicationRef);

    this._zoneSubscription = ngZone.onStable.subscribe(() => {
      if (this.isPopupOpen()) {
        const host = this.positionTo || this._elementRef.nativeElement;

        positionElements(
          host, this._windowRef.location.nativeElement, this.placement,
          this.container === 'body');
      }
    });

    this._setValueSubscription$ = ngZone.onStable.pipe(take(1)).subscribe(() => {
      this._inputValueBackup = this._elementRef.nativeElement.value;
    });
  }

  ngOnInit(): void {
    this._isIE = this._deviceDetection.isIE();

    const inputValues$ = this._valueChanges.pipe(
      distinctUntilChanged(),
      tap(value => {
        this._inputValueBackup = value;

        if (this.editable) {
          this._onChange(value);
        }
      }));
    const focus$ = merge(inputValues$, this._openOnFocus$);
    const results$ = focus$.pipe(this.ngbTypeahead);
    const processedResults$ = results$.pipe(skipWhile(() => this.preventResults)).pipe(tap((val) => {
      if (!this.editable) {
        const correctValue = val.filter(function (v: any): boolean {
          let newValue = this.backupValue;

          if (newValue) {
            newValue = newValue.toLowerCase();
          }

          if (this.formatter) {
            v = this.formatter(v);
          }

          return newValue === v.toLowerCase();

          // if (typeof v === 'string' || v instanceof String) {
          //   return newValue === v.toLowerCase();
          // } else if (v.name) { // TODO: change logic to work with any object data
          //   return newValue === v.name.toLowerCase();
          // } else if (v.dataValue) {
          //   return newValue === v.dataValue.toLowerCase();
          // } else {
          //   return false;
          // }
        }, {'backupValue': this._inputValueBackup, 'formatter': this.inputFormatter});

        val.length ? this._onChange(correctValue[0]) : this._onChange(undefined);
      }
    }));
    const userInput$ = this._resubscribeTypeahead.pipe(switchMap(() => processedResults$));

    this._subscription = this._subscribeToUserInput(userInput$);
  }

  ngOnDestroy(): void {
    this._closePopup();
    this._unsubscribeFromUserInput();
    this._zoneSubscription.unsubscribe();
    this._setValueSubscription$.unsubscribe();
  }

  registerOnChange(fn: (value: any) => any): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: () => any): void {
    this._onTouched = fn;
  }

  writeValue(value: any): void {
    this._inputValueBackup = this._formatItemForInput(value);
    this._writeInputValue(this._formatItemForInput(value));

    // while [(ngModel)] value changed outside need to update the list by correct items
    setTimeout(() => {
      if (this.isPopupOpen() && this._elementRef) {
        this._emitInputEvent();
      }
    });
  }

  setDisabledState(isDisabled: boolean): void {
    this._renderer.setProperty(this._elementRef.nativeElement, 'disabled', isDisabled);
  }

  onDocumentClick(event: any): void {
    if (event.target !== this._elementRef.nativeElement) {
      this.dismissPopup();
    }
  }

  /**
   * Dismisses typeahead popup window
   */
  dismissPopup(): void {
    if (this.isPopupOpen()) {
      this._closePopup();
      this._writeInputValue(this._inputValueBackup);
    }
  }

  /**
   * Returns true if the typeahead popup window is displayed
   */
  isPopupOpen(): boolean {
    return this._windowRef != null;
  }

  handleBlur(event: FocusEvent): void {
    this.onBlur.emit(event);

    this._resubscribeTypeahead.next(null);
    this._onTouched();
  }

  handleFocus(event: FocusEvent): void {
    this.onFocus.emit(event);

    if (this.openOnFocus) {
      this._openOnFocus$.next(this._inputValueBackup);

      return;
    }

    this.typeAheadPopupOpen.emit({text: this._inputValueBackup, open: false});
  }

  handleKeyDown(event: KeyboardEvent): void {
    if (!this.isPopupOpen()) {
      return;
    }

    const keyCode = getEventKeyCode(event);

    if (Key[toString(keyCode)]) {
      switch (keyCode) {
        case Key.ArrowDown:
          event.preventDefault();
          this._windowRef.instance.next();
          this._showHint();
          break;
        case Key.ArrowUp:
          event.preventDefault();
          this._windowRef.instance.prev();
          this._showHint();
          break;
        case Key.Enter:
        case Key.Tab: {
          const result = this._windowRef.instance.getActive();

          if (isDefined(result)) {
            event.preventDefault();
            event.stopPropagation();
            this._selectResult(result);
          }
          this._closePopup();
          break;
        }
      }
    }
  }

  private _onTouched: () => void = (): void => {
    return;
  };

  private _onChange: (_: any) => void = (): void => {
    return;
  };

  private _openPopup(): void {
    if (!this.isPopupOpen()) {
      this.typeAheadPopupOpen.emit({text: this._inputValueBackup, open: true});
      this._inputValueBackup = this._elementRef.nativeElement.value;
      this._windowRef = this._popupService.open();
      this._windowRef.instance.id = this.popupId;
      this._windowRef.instance.hostClass = this.popupClass;
      this._windowRef.instance.appendedToBody = this.container === 'body';
      this._windowRef.instance.selectEvent.subscribe((result: any) => this._selectResultClosePopup(result));
      this._windowRef.instance.activeChangeEvent.subscribe((activeId: string) => this.activeDescendant = activeId);
      const menu = this._windowRef.location.nativeElement;

      if (this.container) {
        if (this.container === 'body') {
          document.body.appendChild(menu);
        } else {
          this.container.appendChild(menu);
        }
      }

      this._layerService.addComponent(this, (event) => {
        event.preventDefault();
        this._resubscribeTypeahead.next(null);
        this.dismissPopup();
      });

      if (!isDefined(this._documentListenerFunc)) {
        this._documentListenerFunc = this._renderer.listen('document', 'click', this.onDocumentClick.bind(this));
      }
    }
  }

  private _closePopup(): void {
    this._popupService.close();
    this._windowRef = null;
    this.activeDescendant = undefined;
    this._layerService.removeComponent(this);
    this.typeAheadPopupOpen.emit({text: this._inputValueBackup, open: false});

    if (isDefined(this._documentListenerFunc)) {
      this._documentListenerFunc();
      this._documentListenerFunc = null;
    }
  }

  private _selectResult(result: any): void {
    let defaultPrevented = false;

    this.selectItem.emit({
      item: result, preventDefault: () => {
        defaultPrevented = true;
      }
    });
    this._resubscribeTypeahead.next(null);

    if (!defaultPrevented) {
      if (this.inputFormatter) {
        this._inputValueBackup = this.inputFormatter(result);
      } else {
        this._inputValueBackup = result;
      }

      this.writeValue(result);
      this._onChange(result);
    }
  }

  private _selectResultClosePopup(result: any): void {
    this._selectResult(result);
    this._closePopup();
  }

  private _showHint(): void {
    if (this.showHint && this._windowRef.instance.hasActive() && this._inputValueBackup != null) {
      const userInputLowerCase = this._inputValueBackup.toLowerCase();
      const formattedVal = this._formatItemForInput(this._windowRef.instance.getActive());

      if (userInputLowerCase === formattedVal.substr(0, this._inputValueBackup.length).toLowerCase()) {
        this._writeInputValue(this._inputValueBackup + formattedVal.substr(this._inputValueBackup.length));
        this._elementRef.nativeElement.setSelectionRange.apply(
          this._elementRef.nativeElement, [this._inputValueBackup.length, formattedVal.length]);
      } else {
        this._writeInputValue(this._formatItemForInput(this._windowRef.instance.getActive()));
      }
    }
  }

  private _formatItemForInput(item: any): string {
    return item != null && this.inputFormatter ? this.inputFormatter(item) : toString(item);
  }

  private _writeInputValue(value: string): void {
    this._renderer.setProperty(this._elementRef.nativeElement, 'value', toString(value));
  }

  private _subscribeToUserInput(userInput$: Observable<any[]>): Subscription {
    return userInput$.subscribe((results) => {
      if (!results || results.length === 0) {
        this._closePopup();
      } else {
        this._openPopup();
        this._windowRef.instance.focusFirst = this.focusFirst;
        this._windowRef.instance.results = results;
        this._windowRef.instance.term = this._elementRef.nativeElement.value;
        this._windowRef.instance.virtualScroll = this.virtualScroll;
        this._windowRef.instance.virtualScrollItemSize = this.virtualScrollItemSize;
        this._windowRef.instance.virtualScrollDropdownWidth = this.virtualScrollDropdownWidth;
        this._windowRef.instance.virtualScrollDropdownHeight = this.virtualScrollDropdownHeight;

        if (this.resultFormatter) {
          this._windowRef.instance.formatter = this.resultFormatter;
        }

        if (this.resultTemplate) {
          this._windowRef.instance.resultTemplate = this.resultTemplate;
        }
        this._windowRef.instance.resetActive();

        // The observable stream we are subscribing to might have async steps
        // and if a component containing typeahead is using the OnPush strategy
        // the change detection turn wouldn't be invoked automatically.
        SafeCdr.detectChanges(this._windowRef.changeDetectorRef);

        this._showHint();
      }
      const count = results.length;

      this._live.say(count === 0 ? 'No results available' : `${count} result${count === 1 ? '' : 's'} available`);
    });
  }

  private _emitInputEvent(): void {
    const event = new Event('input');

    this._elementRef.nativeElement.dispatchEvent(event);
  }

  private _unsubscribeFromUserInput(): void {
    if (this._subscription) {
      this._subscription.unsubscribe();
    }
    this._subscription = null;
  }
}
