import {DragDropModule} from '@angular/cdk/drag-drop';
import {CommonModule} from '@angular/common';
import {ModuleWithProviders, NgModule} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {AdaptListBuilderItemComponent} from './list-builder-item/list-builder-item.component';
import {AdaptListBuilderComponent} from './list-builder.component';
import {
  AdaptRxLabelModule,
  AdaptTooltipModule,
  AdaptCustomActionsModule,
  AdaptDragonInputHandlerModule,
  AdaptEmptyStateModule,
  AdaptIconModule,
  AdaptPopoverModule,
  AdaptHighlightModule,
  AdaptIconConfig
} from '@bmc-ux/adapt-angular';
import {AdaptTextFieldModule} from '../textfield/index';
import {AdaptTypeaheadSubModule} from '../typeahead/index';
import {AdaptSelectSearchFilter, AdaptSelectSearchFilterModule} from '../select/index';
import {ai_dots} from '@bmc-ux/dpl-iconfont';

@NgModule({
  imports: [
    CommonModule,
    DragDropModule,
    FormsModule,
    AdaptIconModule,
    AdaptTooltipModule,
    AdaptPopoverModule.forRoot(),
    AdaptEmptyStateModule,
    AdaptSelectSearchFilterModule,
    AdaptTextFieldModule,
    AdaptTypeaheadSubModule,
    AdaptDragonInputHandlerModule,
    AdaptRxLabelModule,
    AdaptCustomActionsModule,
    AdaptHighlightModule,
  ],
  declarations: [AdaptListBuilderComponent, AdaptListBuilderItemComponent],
  exports: [AdaptListBuilderComponent]
})
export class AdaptListBuilderModule {
  static forRoot(): ModuleWithProviders<AdaptListBuilderModule> {
    return {
      ngModule: AdaptListBuilderModule,
      providers: [AdaptSelectSearchFilter]
    };
  }

  constructor(iconService: AdaptIconConfig) {
    iconService.registerIcons([ai_dots]);
  }
}

export * from './list-builder.component';
export * from './list-builder.model';
