/* eslint-disable @typescript-eslint/no-explicit-any */

import {Component, EventEmitter, forwardRef, Input, NgZone, Output} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {take} from 'rxjs/operators';
import {AdaptCustomAction, AdaptCustomActionsAlignEnum} from '@bmc-ux/adapt-angular';
import {ListBuilderTextsModel, ListItemsTooltipConfig} from '../list-builder.model';
import {SelectOption} from '../../select/index';

@Component({
  selector: 'adapt-list-builder-item',
  templateUrl: './list-builder-item.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => AdaptListBuilderItemComponent),
      multi: true
    }
  ],
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[attr.id]': 'id',
    '[style.display]': "'block'"
  }
})

export class AdaptListBuilderItemComponent {
  listItem: SelectOption;

  editMode: boolean = false;
  inputControlModel: string = '';
  customActionsAlignedTo: AdaptCustomActionsAlignEnum = AdaptCustomActionsAlignEnum.Right;

  get customActions(): AdaptCustomAction[] {
    return [
      {
        name: this.controlLabels.cancel,
        visible: true,
        onClick: this.cancelEditMode.bind(this),
        onMouseDown: (event) => {
          event.preventDefault();
        }
      },
      {
        name: this.controlLabels.save,
        visible: !this.isItemDisallowed,
        onClick: this.exitEditMode.bind(this),
        onMouseDown: (event) => {
          event.preventDefault();
        }
      }
    ];
  }

  @Input() id: string = '';
  @Input() query: string = '';
  @Input() controlLabels: ListBuilderTextsModel;
  @Input() itemActionsTooltipConfig: ListItemsTooltipConfig;
  @Input() hideControls: boolean = false;
  @Input() isItemDisallowed: boolean = false;
  @Input() hideEdit: boolean = false;
  @Input() hideDelete: boolean = false;
  @Input() draggable: boolean = true;
  @Input() disabled: boolean = false;

  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onEdit: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onDelete: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onSave: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onCancel: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onKeyUp: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onKeyDown: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onEditChange: EventEmitter<any> = new EventEmitter();

  constructor(private _ngZone: NgZone) {
  }

  /**
   * Save value and exit edit mode
   */
  exitEditMode(): void {
    this.editMode = false;
    this.listItem.name = this.inputControlModel;
    this.onSave.emit(this.listItem);
    this.onEditChange.emit('');
  }

  /**
   * Exit edit mode without saving
   */
  cancelEditMode(): void {
    this.editMode = false;

    this.onCancel.emit(this.listItem);
    this.onEditChange.emit('');
  }

  enterEditMode(): void {
    this.editMode = true;
    this.inputControlModel = this.listItem.name;
  }

  deleteItem(): void {
    this.onDelete.emit(this.listItem);
  }

  editChange(): void {
    this.onEditChange.emit(this.inputControlModel);
  }

  onUp(e: any): void {
    e.preventDefault();
    this.onKeyUp.emit();

    this._ngZone.onStable
      .pipe(take(1))
      .subscribe((() => {
        e.target.focus();
      }).bind(e));
  }

  onDown(e: any): void {
    e.preventDefault();
    this.onKeyDown.emit();
  }

  writeValue(value: SelectOption): void {
    if (value) {
      this.listItem = value;
    }
  }

  registerOnChange(fn: any): void {
    this.propagateChange = fn;
  }

  registerOnTouched(): void {
    return;
  }

  // ### ControlValueAccessor implementation
  private propagateChange: () => void = () => {
    return;
  };
}
