import {
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Injector,
  Input,
  OnInit,
  Output,
  TemplateRef,
  ViewChild
} from '@angular/core';
import {noop, Observable, OperatorFunction} from 'rxjs';
import {ControlValueAccessor, NG_VALUE_ACCESSOR, NgControl} from '@angular/forms';
import {transition, trigger, useAnimation} from '@angular/animations';
import {ResultTemplateContext} from './typeahead-window';
import {AdaptTypeaheadConfig} from './typeahead.config';
import {
  AdaptRadarAngularCustomEventName,
  AdaptTranslateService,
  ANIMATION_NAME,
  hasRequiredValidator,
  PlacementArray,
  toBoolean
} from '@bmc-ux/adapt-angular';
import {AdaptRadarGenericDirective, AdaptRadarService} from '@bmc-ux/adapt-radar';


let nextId = 0;

export const TYPEAHEAD_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => AdaptTypeaheadComponent),
  multi: true
};

@Component({
  selector: 'adapt-typeahead',
  template: `
    <div class="dropdown show"
         [class.has-warning]="isWarning && !isError"
         [class.has-danger]="!isWarning && isError">
      <adapt-rx-control-label [for]="id"
                              [label]="label"
                              [requiredLabel]="requiredLabel"
                              [subLabel]="description"
                              [showRequiredLabel]="required"></adapt-rx-control-label>
      <input #input
             type="text"
             class="form-control"
             [attr.id]="id"
             [(ngModel)]="value"
             [ngbTypeahead]="typeahead"
             [preventResults]="disabled || readonly"
             [disabled]="disabled"
             [readOnly]="readonly"
             [container]="appendToContainer"
             [virtualScroll]="virtualScroll"
             [virtualScrollItemSize]="virtualScrollItemSize"
             [virtualScrollDropdownWidth]="virtualScrollDropdownWidth"
             [virtualScrollDropdownHeight]="virtualScrollDropdownHeight"
             [positionTo]="positionTo"
             [placement]="placement"
             [required]="required"
             [editable]="editable"
             [showHint]="showHint"
             [focusFirst]="focusFirst"
             [inputFormatter]="inputFormatter"
             [resultFormatter]="resultFormatter"
             [resultTemplate]="resultTemplate"
             [placeholder]="placeholder || ''"
             [openOnFocus]="openOnFocus"
             [autocomplete]="autocomplete"
             [attr.aria-label]="ariaLabel"
             [attr.aria-labelledby]="ariaLabelledby"
             [attr.aria-describedby]="ariaDescribedBy"
             (selectItem)="onSelect($event)"
             (typeAheadPopupOpen)="typeAheadPopUpChange($event)"
             (click)="click.emit($event)"
             (onBlur)="onBlur($event)"
             (onFocus)="onFocus($event)"
             (input)="onInputChange($event)"/>

      <div *ngIf="errorMessage && isError && !isWarning" [@validationFeedbackAnimation] role="alert">
        <p class="form-control-feedback m-0 pt-1 pb-2">{{errorMessage}}</p>
      </div>
      <div *ngIf="warningMessage && isWarning && !isError" [@validationFeedbackAnimation] role="alert">
        <p class="form-control-feedback m-0 pt-1 pb-2">{{warningMessage}}</p>
      </div>
    </div>`,
  animations: [
    trigger('validationFeedbackAnimation', [
      transition(':enter', [
        useAnimation(ANIMATION_NAME.fadeIn)
      ]),
      transition(':leave', [
        useAnimation(ANIMATION_NAME.fadeOut)
      ])
    ])
  ],
  providers: [TYPEAHEAD_VALUE_ACCESSOR],
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {'class': 'typeahead'}
})
export class AdaptTypeaheadComponent extends AdaptRadarGenericDirective implements ControlValueAccessor, OnInit {
  appendToContainer: HTMLElement | 'body';
  /**
   * A function to transform the provided observable text into the array of results.
   * Note that the "this" argument is undefined so you need to explicitly bind it to a desired "this" target.
   */

  /*
    * Typeahead OperatorFunction
  */
  @Input() set typeahead(inputFunction: OperatorFunction<string, any[]> | Observable<string>) {
    this._typeahead = inputFunction;
  }

  get typeahead(): OperatorFunction<string, any[]> | Observable<string> {
    return this._typeahead;
  }

  /**
   * The plain text label for the input element
   */
  @Input() label: string;

  /**
   * The description for the input element
   */
  @Input() description: string;

  /**
   * Unique id of the element.
   */
  @Input() id: string = `typeahead-${nextId++}`;

  /**
   * A flag indicating if model values should be restricted to the ones selected from the popup only.
   */
  @Input() editable: boolean;

  /**
   * A flag indicating if the first match should automatically be focused as you type.
   */
  @Input() focusFirst: boolean;

  /**
   * Show hint when an option in the result list matches.
   */
  @Input() showHint: boolean;

  /**
   * A function to convert a given value into string to display in the input field
   */
  @Input() inputFormatter: Function;

  /**
   * A function to format a given result before display.
   * This function should return a formatted string without any HTML markup
   */
  @Input() resultFormatter: Function;

  /**
   * A template to override a matching result default display
   */
  @Input() resultTemplate: TemplateRef<ResultTemplateContext>;

  /**
   * Input field placeholder
   */
  @Input() placeholder: string;

  /**
   * Placement of a typeahead accepts:
   * "top", "top-left", "top-right", "bottom", "bottom-left", "bottom-right",
   * "left", "left-top", "left-bottom", "right", "right-top", "right-bottom"
   * and array of above values.
   */
  @Input() placement: PlacementArray = 'auto';

  /**
   * Input field errorMessage
   */
  @Input() errorMessage: string;

  /**
   * Warning is displayed when user enter some text which is not corresponding any typeahead options.
   * @since 11.16.0
   */
  @Input() warningMessage: string;

  @Input() disabled: boolean = false;

  @Input() readonly: boolean = false;

  /**
   * Append to body trigger
   */
  @Input() set appendToBody(state: boolean) {
    if (state && !this.appendToContainer) {
      this.appendTo = 'body';
    }
  }

  /**
   * When specified, the drop-down list will be anchored to the selected element
   */
  @Input() positionTo: HTMLElement;

  /**
   * When specified, the drop-down list will be appended to the selected element
   */
  @Input() set appendTo(element: HTMLElement | 'body') {
    this.appendToContainer = element;
  }

  /**
   * Whether the input is required. The plain text 'requiredLabel' for the input element
   */
  @Input() requiredLabel: string;

  /**
   * Open popup on focus
   */
  @Input() openOnFocus: boolean = false;

  /**
   * Enable virtual scroll for results window. Can be suitable for big amount of results
   */
  @Input() virtualScroll: boolean = false;

  /**
   * Used to set "itemSize" virtual scroll @Input()
   */
  @Input() virtualScrollItemSize: number = 30;

  /**
   * Used to set width for results dropdown
   */
  @Input() virtualScrollDropdownWidth: string = '25.625rem';

  /**
   * Used to set height for results dropdown
   */
  @Input() virtualScrollDropdownHeight: string = '13.75rem';

  /**
   * Attached to the aria-label attribute of the host element. In most cases, arial-labelledby will
   * take precedence so this may be omitted.
   */
  @Input('aria-label') ariaLabel: string | null = null;

  /**
   * Users can specify the `aria-labelledby` attribute which will be forwarded to the input element
   */
  @Input('aria-labelledby') ariaLabelledby: string | null = null;

  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('aria-describedby') ariaDescribedBy: string | null = null;

  /**
   * The value for the `autocomplete` attribute for the `<input>` element.
   * @since v10.14.0
   */
  @Input() autocomplete: string = 'off';

  /**
   * Reference to input element
   */
  @ViewChild('input', {static: true}) inputElement: ElementRef;

  /**
   * An event emitted when a match is selected. Event payload is of type NgbTypeaheadSelectItemEvent.
   */
  @Output() readonly selectItem: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly focus: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly blur: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly click: EventEmitter<any> = new EventEmitter();
  /**
   * If the error state should be shown to user
   */
  isError: boolean;

  /** Make input borders orange in case if user enter wrong text which is not correspond to any typeahead options */
  isWarning: boolean;
  /**
   * The internal data model
   */
  private innerValue: any = '';

  /** Actual value, event if it is not matching any typeahead options */
  private actualInputValue: string = '';

  /**
   * If the typeAhead drop down is open
   */
  private isTypeAheadOpen: boolean = true;
  /**
   * Placeholders for the callbacks which are later provided by the Control Value Accessor
   */
  private onTouchedCallback: () => void = noop;
  private onChangeCallback: (_: any) => void = noop;
  private _isTouched: boolean = false;

  private _required: boolean = false;
  private _typeahead: OperatorFunction<string, any[]> | Observable<string>;

  private _ngControl: NgControl;

  /** Whether the element is required. */
  @Input()
  get required(): any {
    return this._required || hasRequiredValidator(this._ngControl);
  }

  set required(value: any) {
    this._required = toBoolean(value);
  }

  /**
   * get accessor
   */
  get value(): any {
    return this.innerValue;
  }

  /**
   * set accessor including call the onchange callback
   */
  set value(v: any) {
    if (v !== this.innerValue) {
      this.innerValue = v;
      this.onChangeCallback(v);
    }
    this.updateError();
  }

  constructor(config: AdaptTypeaheadConfig,
              private ts: AdaptTranslateService,
              private _injector: Injector,
              _adaptRadarService: AdaptRadarService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptTypeahead);

    this.editable = config.editable;
    this.focusFirst = config.focusFirst;
    this.showHint = config.showHint;
    this.requiredLabel = ts.getCurrentLanguage()['adapt.common.required'];
  }

  ngOnInit(): void {
    this._ngControl = this._injector.get(NgControl, null);
  }

  onFocus(event: FocusEvent): void {
    this.focus.emit(event);
  }

  onSelect(event: any): void {
    this.selectItem.emit(event);
  }

  /**
   * Triggered when typeAhead drop down is open or closed
   */
  typeAheadPopUpChange(event: any): void {
    this.isTypeAheadOpen = event.open;

    // When input text is empty set as empty instead of undefined
    if (!event.text) {
      this.innerValue = '';
    }

    this.updateError();
  }

  /**
   * Update the error UI
   */
  updateError(): void {
    if (!this._isTouched && (this.isTypeAheadOpen || this.required)) {
      this.isError = false;

      return;
    }

    this.isError = ((this.required && !this.innerValue) || (!this.editable && this.innerValue === undefined)) && !this.actualInputValue;
  }

  updateWarning(): void {
    this.isWarning = !this.editable && this._isTouched
      && !this.innerValue && this.actualInputValue && !this.isTypeAheadOpen;
  }

  onInputChange(event: Event): void {
    this.actualInputValue = event.target['value'];
  }

  /**
   * Set touched on blur
   */
  onBlur(event: FocusEvent): void {
    this._isTouched = true;
    this.onTouchedCallback();
    this.blur.emit(event);
    this.updateError();
    this.updateWarning();
  }

  /**
   * From ControlValueAccessor interface
   */
  writeValue(value: any): void {
    if (value !== this.innerValue) {
      this.innerValue = value;
    }
    this.updateError();
  }

  /**
   * From ControlValueAccessor interface
   */
  registerOnChange(fn: any): void {
    this.onChangeCallback = fn;
  }

  /**
   * From ControlValueAccessor interface
   */
  registerOnTouched(fn: any): void {
    this.onTouchedCallback = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}
